@import "../../assets/style/app";

.first {
  padding-top: 45px;

  @include desktop {
    padding-top: 169px;
  }

  .logo {
    display: block;
    width: 204px;
    height: auto;
    margin-left: auto;
    margin-right: auto;

    @include desktop {
      width: calc((100vw * 704 / 1920));
    }
  }

  .leftMobile {
    @include onlyMobile();
    width: 100%;
    height: auto;
    margin-top: 46px;
  }

  .rightMobile {
    @include onlyMobile();
    width: 100%;
    height: auto;
    margin-top: -25px;
  }

  .leftDesktop {
    @include onlyDesktop();
    position: absolute;
    left: 0;
    top: 160px;
    width: calc((100vw * 572 / 1920));
  }

  .rightDesktop {
    @include onlyDesktop();
    position: absolute;
    right: 0;
    top: 160px;
    width: calc((100vw * 572 / 1920));
  }

  .videoWrapper {
    position: relative;
    border: 1px solid #5fff74;
    border-radius: 30px;
    width: 279px;
    height: 159px;
    background: #1e1e1e;
    margin-top: 45px;
    margin-left: auto;
    margin-right: auto;

    @include desktop {
      border: 2px solid #07e53b;
      border-radius: 30px;
      width: 865px;
      height: 460px;
      margin-top: 240px;
    }

    .play {
      @include button;
      @include absoluteCenter;
      width: 49px;
      height: 43px;

      @include desktop {
        width: 103px;
        height: 102px;
      }

      svg {
        width: 49px;
        height: 43px;

        @include desktop {
          width: 103px;
          height: 102px;
        }
      }
    }
  }

  .car {
    display: block;
    width: 100%;
    height: auto;
    margin-top: 12px;

    @include desktop {
      margin-top: 36px;
    }
  }


}