@import "../../assets/style/app";

.howItWorks {
  padding-top: 24px;

  @include desktop {
    padding-top: 80px;
  }

  .car_top {
    display: block;
    width: 100%;
    height: auto;
  }

  .wrapper {
    padding-top: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include desktop {
      padding-top: 100px;
    }

    .title {
      font-weight: 600;
      font-size: 25px;
      color: #fff;

      @include desktop {
        font-size: 56px;
      }
    }

    .itemsMobile {
      @include onlyMobile(flex);
      margin-top: 6px;
      flex-direction: column;
      align-items: center;

      .itemMobile {
        @include centerColumn;
        position: relative;
        border: 1px solid #5fff74;
        border-radius: 24px;
        width: 332px;
        height: 140px;
        backdrop-filter: blur(11.899999618530273px);
        background: rgba(0, 0, 0, 0.53);
        padding: 0px 16px;

        &:not(:first-child) {
          margin-top: 30px;
        }

        img {
          display: block;
          height: 42px;
          width: auto;
        }

        p {
          font-size: 13px;
          text-align: center;
          color: #fff;
          margin-top: 16px;
        }

        svg {
          position: absolute;
          width: 14px;
          height: 19px;
          left: 50%;
          bottom: 0;
         transform: translate(-50%, 100%);
        }
      }

    }

    .itemsDesktop {
      @include onlyDesktop(flex);
      justify-content: center;
      border-radius: 66px;
      width: 1359px;
      height: 330px;
      background: rgba(0, 0, 0, 0.69);
      padding-top: 57px;
      margin-top: 70px;

      .itemDesktop {
        display: flex;
        flex-direction: column;
        position: relative;
        width: 276px;


        &:not(:first-child) {
          margin-left: 60px;
        }

        img {
          display: block;
          height: 80px;
          width: auto;
          flex-shrink: 0;
          align-self: center;
        }

        p {
          font-size: 20px;
          text-align: center;
          color: #fff;
          margin-top: 36px;
        }

        svg {
          position: absolute;
          width: 36px;
          height: 24px;
          top: 32px;
          right: -48px;
        }
      }

    }



  }

  .car_bottom {
    display: block;
    width: 100%;
    height: auto;
  }

}