@import "../../assets/style/app";

.live {

  .title {
    display: block;
    width: 339px;
    height: auto;
    margin-left: auto;
    margin-right: auto;

    @include desktop {
      width: 1298px;
    }
  }

  .cards {
    @include container;
    margin-top: 24px;

    @include desktop {
      margin-top: 220px;
    }

    .cards_inner {
      @include inner;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(3, 1fr);
      grid-gap: 48px 16px;
      width: 330px;
      margin-left: auto;
      margin-right: auto;

      @include desktop {
        grid-gap: 190px 112px;
        width: 1670px;
      }

      .card {
        position: relative;
        border-radius: 24px 24px 18px 18px;
        border: 1px solid #5fff74;
        background: rgba(0, 0, 0, 0.71);

        @include desktop {

        }


        .imgWrapper {
          @include centerRow;
          border-radius: 24px;
          border: 1px solid #5fff74;
          background: #0d0e10;
          height: 148px;
          overflow: hidden;

          @include desktop {
            height: 751px;
          }

          img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
          }

        }

        &:nth-child(4) .imgWrapper img,
        &:nth-child(5) .imgWrapper img,
        &:nth-child(6) .imgWrapper img {
          width: 133px;
          height: 54px;

          @include desktop {
            width: 662px;
            height: 269px;
          }
        }

        .label {
          position: absolute;
          left: 0;
          display: block;
        }

        &:nth-child(1) .label {
          width: 94px;
          height: auto;
          top: -22px;

          @include desktop {
            width: 476px;
            top: -112px;
          }
        }

        &:nth-child(2) .label {
          width: 109px;
          height: auto;
          top: -25px;

          @include desktop {
            width: 552px;
            top: -126px;
          }
        }

        &:nth-child(3) .label {
          width: 107px;
          height: auto;
          top: -25px;

          @include desktop {
            width: 533px;
            top: -152px;
          }
        }

        &:nth-child(4),
        &:nth-child(5),
        &:nth-child(6) {
          .label {
            width: 94px;
            height: auto;
            top: -25px;
            left: -7px;

            @include desktop {
              width: 468px;
              top: -124px;
              left: -35px;
            }
          }
        }


        .cardBottom {
          padding: 4px 6px 12px;

          @include desktop {
            padding: 24px 24px 64px;
          }

          .bottomHeader {
            display: flex;
            align-items: flex-end;
            justify-content: space-between;

            .headerLeft {
              display: flex;
              flex-direction: column;
              justify-content: center;

              p:nth-child(1) {
                @include PoppinsFont;
                white-space: nowrap;
                font-weight: 500;
                font-size: 15px;
                color: #fff;

                @include desktop {
                  font-size: 69px;
                }
              }

              p:nth-child(2) {
                @include PoppinsFont;
                white-space: nowrap;
                font-weight: 500;
                font-size: 6px;
                color: #fff;

                @include desktop {
                  font-size: 28px;
                }
              }
            }

            .headerRight {
              display: flex;
              flex-direction: column;
              justify-content: center;

              p:nth-child(1) {

                span:nth-child(1) {
                  @include PoppinsFont;
                  white-space: nowrap;
                  font-weight: 500;
                  font-size: 16px;
                  line-height: 150%;
                  text-align: center;
                  color: #999;

                  @include desktop {
                    font-size: 90px;
                  }
                }

                span:nth-child(2) {
                  @include PoppinsFont;
                  white-space: nowrap;
                  font-weight: 500;
                  font-size: 5px;
                  line-height: 150%;
                  text-align: center;
                  color: #999;

                  @include desktop {
                    font-size: 21px;
                  }
                }
              }

              p:nth-child(2) {
                @include PoppinsFont;
                font-weight: 500;
                font-size: 5px;
                line-height: 150%;
                text-align: right;
                color: #999;

                @include desktop {
                  font-size: 25px;
                }
              }
            }


          }

          .timer {
            margin-top: 5px;
            display: flex;
            justify-content: center;

            @include desktop {
              margin-top: 24px;
            }

            .timerItem {
              display: flex;
              flex-direction: column;
              align-items: center;

              &:not(:first-child) {
                margin-left: 7px;

                @include desktop {
                  margin-left: 32px;
                }
              }

              .itemValue {
                @include centerRow;
                border: 1px solid rgba(255, 255, 255, 0.2);
                width: 20px;
                height: 20px;
                background: rgba(0, 0, 0, 0.34);

                @include desktop {
                  width: 104px;
                  height: 101px;
                }

                span {
                  @include ExoFont;
                  font-weight: 400;
                  font-size: 10px;
                  color: #fff;

                  @include desktop {
                    font-size: 47px;
                  }
                }


              }

              .itemLabel {
                @include ExoFont;
                font-weight: 400;
                font-size: 7px;
                color: #fff;
                margin-top: 1px;

                @include desktop {
                  font-size: 24px;
                  margin-top: 5px;
                }
              }
            }
          }

          .buttons {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 5px;

            @include desktop {
              margin-top: 50px;
            }

            button {
              @include button;
              @include centerRow;
            }

            .signBtn {
              width: 16px;
              height: 16px;
              background: #FFF;
              border-radius: 50%;

              @include desktop {
                width: 76px;
                height: 76px;
              }

              span {
                @include ExoFont;
                font-weight: 600;
                font-size: 16px;
                color: #000;

                @include desktop {
                  font-size: 71px;
                }
              }
            }

            .add {
              border-radius: 109px;
              width: 98px;
              height: 16px;
              background: #fff;
              margin: 0px 6px;

              @include desktop {
                width: 496px;
                height: 81px;
                margin: 0px 24px;
              }

              img {
                display: block;
                width: 12px;
                height: auto;

                @include desktop {
                  width: 60px;
                }
              }

              span {
                @include ExoFont;
                font-weight: 600;
                font-size: 8px;
                color: #000;
                margin-left: 4px;

                @include desktop {
                  font-size: 32px;
                  margin-left: 30px;
                }
              }
            }

          }


        }


      }
    }
  }

}