@import "../../assets/style/app";

.login {
  @include container;
  background-image: url(../../assets/png/loginBackground.png);
  background-size: cover;
  min-height: 100vh;
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 54px;

  @include desktop {
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
    @include centerColumn;
  }

  .card {

    @include desktop {
      margin-left: auto;
      margin-right: auto;
      border-radius: 86px;
      width: 748px;
      //height: 742px;
      backdrop-filter: blur(15.5px);
      background: rgba(0, 0, 0, 0.5);
      padding: 45px 104px;
    }

    .title {
      @include InterFont;
      font-weight: 500;
      font-size: 26px;
      color: #5fff74;

      @include desktop {
        font-size: 26px;
      }
    }

    .text {
      @include InterFont;
      font-weight: 500;
      font-size: 18px;
      color: #999;

      @include desktop {
        font-size: 23px;
      }
    }

    .inputs {
      margin-top: 36px;

      .inputWrapper {
        position: relative;
        display: flex;
        align-items: center;
        border: 2px solid #5fff74;
        border-radius: 10px;
        padding: 0 20px;
        height: 58px;
        background: #000;

        @include desktop {
          //height: 84px;
        }

        &:not(:first-child) {
          margin-top: 10px;
        }

        & > svg {
          width: 24px;
          height: 24px;
        }

        input {
          background: none;
          border: none;
          outline: none;
          @include InterFont;
          font-weight: 400;
          font-size: 16px;
          color: #c7c7c7;
          margin-left: 10px;
          width: 100%;

          @include desktop {

          }
        }

        .hide {
          @include button;
          position: absolute;
          width: 24px;
          height: 24px;
          top: 50%;
          transform: translateY(-50%);
          right: 20px;

          svg {
            width: 24px;
            height: 24px;
          }

          &_disable {
            opacity: 0.5;
          }
        }
      }
    }

    .checkWrapper {
      margin-top: 18px;
      display: flex;

      .btn {
        @include button;
        width: 24px;
        height: 24px;

        svg {
          width: 24px;
          height: 24px;

          path {
            fill: grey;
          }
        }

        &_check {
          svg {

            path {
              fill: #5FFF74;
            }
          }
        }
      }

      p {
        @include InterFont;
        font-weight: 400;
        font-size: 16px;
        line-height: 162%;
        color: #999;
        margin-left: 10px;

        a {
          color: #169c89;
        }
      }
    }


    .forgot {
      @include InterFont;
      font-weight: 400;
      font-size: 16px;
      color: #5fff74;
      margin-top: 18px;
    }

    .loginBtn {
      @include button;
      @include centerRow;
      border-radius: 10px;
      height: 58px;
      width: 100%;
      background: #5fff74;
      margin-top: 18px;

      span {
        @include InterFont;
        font-weight: 400;
        font-size: 20px;
        color: #000;
      }
    }

    .label {
      @include InterFont;
      font-weight: 400;
      font-size: 16px;
      color: #999;
      text-align: center;
      margin-top: 40px;

      @include desktop {
        margin-top: 21px;
      }

    }

    .buttons {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-column-gap: 6px;
      margin-top: 18px;

      @include desktop {
        grid-column-gap: 18px;
      }

      button {
        @include button;
        @include centerRow;
        border: 2px solid #ddd;
        border-radius: 10px;
        height: 50px;
        background: #fff;

      }
    }

  }
}