@import "../../assets/style/app";

.paymentSuccessful {
  display: flex;
  align-items: center;

  background-image: url(../../assets/png/loginBackground.png);
  background-size: cover;
  min-height: 100vh;
  padding-top: 40px;
  padding-bottom: 40px;

  @include desktop {
    padding-top: 160px;
    padding-bottom: 160px;
    justify-content: center;
  }

  .card {
    position: relative;
    backdrop-filter: blur(15.5px);
    background: rgba(0, 0, 0, 0.5);
    border-radius: 49px;
    padding: 25px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;

    @include desktop {
      border-radius: 86px;
      width: 748px;
      padding: 100px;
      flex-grow: 0;
    }

    .icon {
      @include centerRow;
      align-self: center;
      width: 90px;
      height: 90px;
      border-radius: 50%;
      border: 18px solid #07431A;
      background: #00e540;

      @include desktop {
        width: 133px;
        height: 133px;
        border: 26px solid #07431A;
      }

      svg {
        width: 28px;
        height: 18px;

        @include desktop {
          width: 38px;
          height: 26px;
        }
      }

    }

    .title {
      font-weight: 700;
      font-size: 25px;
      text-align: center;
      color: #fff;
      margin-top: 8px;

      @include desktop {
        font-size: 48px;
        margin-top: 24px;
      }
    }

    .text {
      margin-top: 8px;
      font-weight: 400;
      font-size: 12px;
      text-align: center;
      color: #fff;
      padding-bottom: 8px;
      border-bottom: 1px solid #FFF;

      @include desktop {
        margin-top: 32px;
        font-size: 20px;
        padding-bottom: 22px;
      }
    }

    .info {
      margin-top: 40px;
      padding-bottom: 12px;
      border-bottom: 1px dashed #FFF;

      @include desktop {
        padding-bottom: 22px;
      }

      .row {
        display: flex;
        justify-content: space-between;

        &:not(:first-child) {
          margin-top: 7px;
        }

        p {
          font-weight: 400;
          font-size: 16px;
          color: #fff;

          @include desktop {
            font-size: 20px;
          }
        }
      }

    }

    .total {
      margin-top: 10px;
      display: flex;
      justify-content: space-between;

      @include desktop {
        margin-top: 26px;
      }

      p {
        font-weight: 400;
        font-size: 16px;
        color: #fff;

        @include desktop {
          font-size: 20px;
        }
      }
    }

  }

  .thank {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -30px;
    font-style: italic;
    font-weight: 400;
    font-size: 15px;
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: #fff;
    white-space: nowrap;

    @include desktop {
      bottom: -108px;
      font-size: 20px;
    }
  }
}