@import "../../assets/style/app";

.checkoutPage {
  @include container;
  background-image: url(../../assets/png/loginBackground.png);
  background-size: cover;
  min-height: 100vh;
  padding-top: 40px;
  padding-bottom: 40px;

  @include desktop {
    padding-top: 160px;
    padding-bottom: 160px;
  }

  .inner {
    @include inner;

    .title {
      @include InterFont;
      font-weight: 600;
      font-size: 27px;
      color: #fff;
      text-align: center;

      @include desktop {
        font-size: 38px;
      }
    }

    .content {
      display: flex;
      flex-direction: column;
      margin-top: 16px;

      @include desktop {
        flex-direction: row;
        align-items: flex-start;
        margin-top: 48px;
      }

      .left {

        @include desktop {
          flex-grow: 1;
        }

        .card:nth-child(2) {
          margin-top: 20px;

          @include desktop {
            margin-top: 40px;
          }
        }
      }

      .card {
        border-radius: 25px;
        background: rgba(0, 0, 0, 0.5);
        padding: 15px 22px;

        .cardTitle {
          font-weight: 700;
          font-size: 25px;
          color: #fff;
          border-bottom: 1px solid #FFF;
          padding-bottom: 13px;
          margin-bottom: 25px;

          @include desktop {
            font-size: 30px;
          }
        }

        .inputWrapper {
          position: relative;
          display: flex;
          align-items: center;
          background: #000;
          border-radius: 999px;
          height: 34px;
          padding: 0px 12px;

          @include desktop {
            height: 55px;
            padding: 0px 24px;
          }

          input {
            border: none;
            background: none;
            outline: none;
            font-weight: 700;
            font-size: 20px;
            color: #fff;
          }

          svg {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 16px;
            width: 35px;
            height: 20px;

            @include desktop {
              width: 57px;
              height: 31px;
            }
          }

          &_mt {
            margin-top: 16px;

            @include desktop {
              margin-top: 24px;
            }
          }
        }

        .inputs4 {
          margin-top: 24px;
          display: grid;
          grid-template-rows: repeat(4, 1fr);
          grid-row-gap: 24px;

          @include desktop {
            grid-template-rows: repeat(2, 1fr);
            grid-template-columns: repeat(2, 1fr);
            grid-column-gap: 24px;
          }

        }

        .fields2 {
          display: flex;
          flex-direction: column;
          margin-top: 16px;

          @include desktop {
            flex-direction: row;
            margin-top: 24px;
          }

          & > div:nth-child(2) {
            margin-top: 16px;

            @include desktop {
              margin-top: 0;
              margin-left: 24px;
            }
          }


        }

        .radios {
          display: flex;

          .radioWrapper {
            cursor: pointer;
            display: flex;
            align-items: center;

            &:not(:first-child) {
              margin-left: 36px;
            }

            .dot {
              width: 24px;
              height: 24px;
              border-radius: 50%;
              background: #FFF;
              border: 7px solid #FFF;

              &_active {
                background: #000;
              }
            }

            p {
              margin-left: 8px;
              font-weight: 700;
              font-size: 20px;
              color: #fff;
            }
          }
        }

        .items {
          padding-bottom: 24px;
          border-bottom: 1px solid #FFF;

          .item {
            display: flex;
            align-items: center;

            &:not(:first-child) {
              margin-top: 8px;

              @include desktop {
                margin-top: 24px;
              }
            }

            .logoWrapper {
              flex-shrink: 0;
              position: relative;
              @include centerRow;
              border-radius: 20px;
              width: 79px;
              height: 71px;
              background: #000;

              @include desktop {
                width: 105px;
                height: 87px;
              }

              img {
                display: block;
                width: 65px;
                height: auto;

                @include desktop {
                  width: 86px;
                }
              }

              p {
                @include centerRow;
                position: absolute;
                top: 0;
                right: 0;
                transform: translateY(-50%);
                background: #787878;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                font-weight: 700;
                font-size: 15px;
                color: #fff;

                @include desktop {
                  width: 25px;
                  height: 25px;
                }
              }


            }

            .middle {
              flex-grow: 1;
              margin-left: 30px;

              @include desktop {
                margin-left: 40px;
              }

              .cardModel {
                font-weight: 700;
                font-size: 18px;
                color: #fff;
              }

              .edits {
                margin-top: 12px;
                display: flex;
                align-items: center;

                .remove {
                  @include button;
                  display: flex;
                  align-items: center;

                  svg {
                    width: 14px;
                    height: 14px;
                  }

                  span {
                    font-weight: 400;
                    font-size: 15px;
                    color: #787878;
                    margin-left: 12px;
                  }
                }

                .edit {
                  @include button;
                  margin-left: 16px;
                  font-weight: 400;
                  font-size: 15px;
                  color: #787878;
                  text-decoration: underline;
                }
              }

            }

            .value {
              flex-shrink: 0;
              font-weight: 700;
              font-size: 20px;
              color: #fff;
              margin-left: 8px;


            }

          }
        }

        .infoBlock {
          padding-top: 24px;
          padding-bottom: 56px;
          border-bottom: 1px solid #FFF;

          .infoBlockItem {
            display: flex;
            align-items: center;
            justify-content: space-between;

            &:not(:first-child) {
              margin-top: 16px;
            }

            p:nth-child(1) {
              font-weight: 400;
              font-size: 20px;
              color: #787878;
            }

            p:nth-child(2) {
              font-weight: 700;
              font-size: 20px;
              color: #FFF;
            }
          }
        }

        .totalBlock {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-top: 24px;

          p:nth-child(1) {
            font-weight: 700;
            font-size: 25px;
            color: #787878;
          }

          p:nth-child(2) {
            font-weight: 700;
            font-size: 25px;
            color: #FFF;
          }
        }

        .checkoutBtn {
          @include button;
          @include centerRow;
          width: 100%;
          margin-top: 24px;
          height: 47px;
          background: #FFF;
          border-radius: 999px;
          font-weight: 700;
          font-size: 17px;
          text-align: center;
          color: #000;

          @include desktop {
            margin-top: 36px;
            font-size: 24px;
            height: 57px;
          }
        }


      }

      & > .card {
        margin-top: 20px;

        @include desktop {
          margin-top: 0;
          margin-left: 60px;
          width: 470px;
        }
      }
    }


  }
}