@import "../../assets/style/app";

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 14px 8px 0;

  @include desktop {
    padding: 72px 12px 0;
    justify-content: flex-end;
  }

  .wrapper {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include desktop {
      justify-content: flex-end;
    }

    .links {
      display: flex;

      @include desktop {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }

      .link {
        @include BeVietnamProFont;
        text-decoration: none;
        font-weight: 500;
        font-size: 8px;
        letter-spacing: 0.07em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 22px;
        }

        &:not(:first-child) {
          margin-left: 8px;

          @include desktop {
            margin-left: 21px;
          }
        }
      }

    }

    .right {
      display: flex;
      align-items: center;

      img {
        display: block;
        width: 12px;
        height: 10px;

        @include desktop {
          width: 62px;
          height: 52px;
        }
      }

      .login {
        @include BeVietnamProFont;
        text-decoration: none;
        font-weight: 400;
        font-size: 8px;
        letter-spacing: 0.07em;
        color: #fff;
        margin-left: 4px;

        @include desktop {
          font-size: 22px;
          margin-left: 30px;
        }
      }

      .register {
        @include centerRow;
        text-decoration: none;
        border-radius: 999px;
        width: 40px;
        height: 11px;
        background: #5fff74;
        margin-left: 6px;

        @include desktop {
          border-radius: 999px;
          width: 134px;
          height: 36px;
          margin-left: 17px;
        }

        span {
          @include BeVietnamProFont;
          font-weight: 400;
          font-size: 8px;
          letter-spacing: 0.07em;
          color: #000;

          @include desktop {
            font-size: 22px;
          }


        }
      }

    }

  }


}